<template>
  <login-box>
    <!-- register header -->
    <default-head header=""></default-head>

    <!--  -->
    <form action="#" class="form-group">
      <div class="form-group-item borderB">
        <label for="phone" class="form-group-item-text"></label>
        <div class="form-group-item-ctn">
          <input type="text" id="phone" class="form-group-item-input" name="phone"
           v-model="forgetInfo.mobile" v-validate="'required'" placeholder="">
          <input type="button" @click="getCode" ref="btnCode" value="" class="get-code get-code-active">
        </div>
      </div>
      <!--<span v-show="errors.has('phone')" class="is-danger">{{ errors.first('phone') }}</span>-->

      <div class="form-group-item borderB">
        <label for="code" class="form-group-item-text"></label>
        <div class="form-group-item-ctn">
          <input type="text" id="code" class="form-group-item-input"
           v-model="forgetInfo.smsCode" name="code" v-validate="'required'" placeholder="">
        </div>
      </div>

      <div class="form-group-item borderB">
        <label for="possword" class="form-group-item-text new-word"></label>
        <div class="form-group-item-ctn">
          <input type="password" id="password" class="form-group-item-input"
          v-model="forgetInfo.password" v-validate="'required|min:6|max:20'"
           name="password" placeholder="">
        </div>
      </div>

      <div class="form-group-item">
        <label for="possword2" class="form-group-item-text new-word"></label>
        <div class="form-group-item-ctn">
          <input type="password" id="password2" class="form-group-item-input"
          v-model="forgetInfo.passwordCopy" data-vv-name="" v-validate="'required|min:6|max:20'" name="passwordCopy"
          placeholder="">
        </div>
      </div>

      <div class="login-button">
        <!-- <div class="item"> -->
          <input type="button" @click.prevent="registerSubmit('person')" value="">
        <!-- </div>
        <div class="item">
          <input type="button" @click.prevent="registerSubmit('company')" value="">
        </div> -->
      </div>
    </form>

  </login-box>
</template>

<script>
import api from '@/api'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import loginBox from '@/components/login/loginBox'

export default {
  components: { defaultHead, loginBox },
  data: function () {
    return {
      forgetInfo: {
        mobile: '',
        password: '',
        passwordCopy: '',
        smsCode: '',
        type: 2
      },
      wait: 60
    }
  },
  methods: {
    registerSubmit (sign) {
      this.$validator.validateAll().then(() => {
        if (this.forgetInfo.password !== this.forgetInfo.passwordCopy) {
          minutils.Toast({ message: '', position: 'top' })
          return
        }
        if (sign === 'person') this.ajaxChangePwd(this.forgetInfo)
        if (sign === 'company') this.ajaxEnterpriseChangePwd(this.forgetInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    getCode () {
      if (!this.forgetInfo.mobile) {
        minutils.Toast({ message: '', position: 'top' })
        return
      }
      this.ajaxSmsCode({mobile: this.forgetInfo.mobile, type: '2'})
    },
    time (e) {
      let btn = this.$refs.btnCode
      if (this.wait === 0) {
        btn.removeAttribute('disabled')
        btn.value = ''
        this.wait = 60
      } else {
        btn.setAttribute('disabled', true)
        btn.value = this.wait + 's'
        this.wait--
        setTimeout(() => this.time(btn), 1000)
      }
    },
    ajaxChangePwd (data) {
      let self = this
      api.changePwd(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          minutils.MessageBox({
            message: '',
            callback () {
              self.$router.push('/login')
            }
          })
        }
      })
    },
    ajaxEnterpriseChangePwd (data) {
      let self = this
      api.enterpriseChangePwd(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          minutils.MessageBox({
            message: '',
            callback () {
              self.$router.push('/login')
            }
          })
        }
      })
    },
    ajaxSmsCode (data) {
      api.smsCode(data).then(response => {
        let result = response.body
        if (result.isSuccess) this.time()
      })
    }
  }
}
</script>

<style lang="less">
  @import '../assets/css/variable.less';

  .login {
    padding-top: .88rem;
  }

  .form-group {
    width: 100%;
    .form-group-item {
      display: flex;
      height: .88rem;
      line-height: .88rem;
      font-size: .32rem;
      padding: 0 .22rem;
      background: #fff;
      .form-group-item-text {
        width: 1.34rem;
        color: @defaultColor;
      }
      .form-group-item-text.new-word {
        width: 2.14rem;
        color: @defaultColor;
      }
      .form-group-item-ctn {
        flex: 1;
        position: relative;
        height: .8rem;
        .form-group-item-input {
          width: 100%;
          border: none;
          font-size: .32rem;
          color: @lightColor;
          &::placeholder {
            color: @lightColor;
          }

        }
        .get-code, .error {
          position: absolute;
          right: 0;
          top: 0;
          color: @activeColor;
        }
        .get-code {
          top: .1rem;
          height: .68rem;
          line-height: .68rem;
          font-size: .3rem;
          border: 1px solid @activeColor;
          border-radius: .1rem;
          background: @whiteColor;
          width: 2.2rem;
          text-align: center;
        }
        .get-code:disabled {
          border-color: @lineColor;
          color: @lineColor;
        }
      }
    }
    .login-button {
      width: 100%;
      height: .98rem;
      padding: 0 .1rem;
      margin-top: 1rem;
      .item {
        width: 50%;
        float: left;
        height: 100%;
        padding: 0 .15rem
      }
      input {
        width: 100%;
        height: 100%;
        border: 0;
        border-radius: .05rem;
        font-size: .34rem;
        background: @activeColor;
        color: @whiteColor;
      }

    }
  }
  .agree {
    font-size: .3rem;
    color: @midleColor;
    padding-left: .22rem;
    padding-top: .2rem;
  }

</style>



